import React from "react";
import { Link } from 'react-router-dom'
import api from "../../api/api";
import { getUser } from "./../../utils/Session";
import { inject, observer } from 'mobx-react/index'
import NProgress from 'nprogress';
import moment from 'moment';
import 'moment/locale/zh-cn';

import 'nprogress/nprogress.css';
import './home.css'
moment.locale('zh-cn');

@inject('articleStore', 'headerStore') @observer

class Home extends React.Component {
    constructor(props) {
        super(props)
        this.props.headerStore.setCurrunt('')
    }
    state = {
        backUserId: 0,
        user: ''
    }
    componentWillMount() {
        console.log(JSON.parse(getUser().split('?')[0]))
        let userInfo = JSON.parse(getUser().split('?')[0])
        this.setState({
            backUserId: userInfo.userToken.backUserId, deptId: userInfo.deptId,
            user: userInfo
        })
        let waitAuthCount = 0
        if (userInfo.backUserId == 1) {
            this.getList(userInfo.userToken.backUserId, '1970/01/01', moment().format('YYYY/MM/DD') + ' 23:59:59', '', '').then(res => {

                waitAuthCount = res.data.waitAuthCount
                this.getList1(userInfo.userToken.backUserId, '1970/01/01', moment().format('YYYY/MM/DD') + ' 23:59:59', '', userInfo.deptId).then(res => {
                    waitAuthCount = waitAuthCount + res.data.waitAuthCount
                    this.getList(userInfo.userToken.backUserId, '1970/01/01', moment().format('YYYY/MM/DD') + ' 23:59:59', '', '', 2).then(res => {

                        waitAuthCount += res.data.waitAuthCount

                        this.getList1(userInfo.userToken.backUserId, '1970/01/01', moment().format('YYYY/MM/DD') + ' 23:59:59', '', userInfo.deptId, 2).then(res => {
                            console.log(res)
                            this.setState({
                                waitAuthCount: waitAuthCount + res.data.waitAuthCount
                            })
                        })

                    })
                })

            })

        } else {
            this.getList(userInfo.userToken.backUserId, '1970/01/01', moment().format('YYYY/MM/DD') + ' 23:59:59', '', userInfo.deptId).then(res => {

                waitAuthCount = res.data.waitAuthCount
                this.getList1(userInfo.userToken.backUserId, '1970/01/01', moment().format('YYYY/MM/DD') + ' 23:59:59', '', userInfo.deptId).then(res => {

                    waitAuthCount = waitAuthCount + res.data.waitAuthCount
                    this.getList(userInfo.userToken.backUserId, '1970/01/01', moment().format('YYYY/MM/DD') + ' 23:59:59', '', userInfo.deptId, 2).then(res => {

                        waitAuthCount += res.data.waitAuthCount
                        this.getList1(userInfo.userToken.backUserId, '1970/01/01', moment().format('YYYY/MM/DD') + ' 23:59:59', '', userInfo.deptId, 2).then(res => {
                            this.setState({
                                waitAuthCount: waitAuthCount + res.data.waitAuthCount
                            })



                        })

                    })
                })

            })

        }

    }
    getList = async (adminId, startTime, endTime, stepSec = '', deptId = '', type = 1, pageNo = 1, pageSize = 10) => {
        let res = ''
        if (adminId == 1) {
            res = await api('superAdminAuthArticleList', 'post', {}, {
                'deptId': deptId == 'no' ? '' : deptId,
                'pageNo': pageNo,
                'pageSize': pageSize,
                'adminId': adminId,
                'startTime': startTime,
                'endTime': endTime,
                'stepSec': stepSec,
                'type': type
            })
        } else {
            res = await api('getWaitAuthArticle', 'post', {}, {
                'deptId': deptId,
                'pageNo': pageNo,
                'pageSize': pageSize,
                'adminId': adminId,
                'startTime': startTime,
                'endTime': endTime,
                'stepFirst': stepSec,
                'type': type

            })
        }

        return res
    }
    getList1 = async (adminId, startTime, endTime, stepSec = '', deptId = '', type = 1, pageNo = 1, pageSize = 10, ) => {
        let res = ''
        if (adminId != 1) {
            res = await api('superAdminAuthArticleList', 'post', {}, {
                'deptId': deptId == 'no' ? '' : deptId,
                'pageNo': pageNo,
                'pageSize': pageSize,
                'adminId': adminId,
                'startTime': startTime,
                'endTime': endTime,
                'stepSec': stepSec,
                'type': type
            })
        } else {
            console.log(this.state.deptId)
            res = await api('getWaitAuthArticle', 'post', {}, {
                'deptId': deptId,
                'pageNo': pageNo,
                'pageSize': pageSize,
                'adminId': adminId,
                'startTime': startTime,
                'endTime': endTime,
                'stepFirst': stepSec,
                'type': type

            })
        }

        return res
    }
    componentDidMount() {
        NProgress.done()
    }
    componentWillUnmount() {
        NProgress.start()
    }
    render() {
        return (
            <div id="home">
                <div className="container">
                    <div className="slider">
                        <div className="user">
                            <img src={this.state.user && this.state.user.headImg} alt="" />
                            <p>{this.state.user && this.state.user.name}</p>
                        </div>
                        <div className="userInfo">
                            <div className="info">
                                <div className="flex_r">
                                    所属部门
                                    </div>
                                <div className="flex">
                                    {this.state.user && this.state.user.deptName}
                                </div>
                            </div>
                            <div className="info">
                                <div className="flex_r">
                                    身份
                                    </div>
                                <div className="flex">
                                    {this.state.user && this.state.user.roleName}

                                </div>
                            </div>
                            <div className="info">
                                <div className="flex_r">
                                    权限
                                    </div>
                                <div className="flex">
                                    {this.state.user && this.state.user.permissionCount}个
                                    </div>
                            </div>
                        </div>
                    </div>
                    <div className="content">
                        <div className="title">待审核</div>
                        <div className="listbox">
                            <div className="list">
                                <div className='normal'>
                                    <img src={require('../../assets/images/img-home-user.png')} alt="" />
                                    <p className="flex">待审核</p>
                                    <div className="flex_r">{this.state.waitAuthCount}</div>
                                </div>
                            </div>
                            <div className="list">
                                <div className='normal'>
                                    <img src={require('../../assets/images/img-home-feedBack.png')} alt="" />
                                    <p className="flex">待反馈</p>
                                    <div className="flex_r">{this.state.user && this.state.user.problemCount}</div>
                                </div>

                            </div> <div className="list">
                                <div className='normal'>

                                    <img src={require('../../assets/images/img-home-lookEdit.png')} alt="" />
                                    <p className="flex">待修改</p>
                                    <div className="flex_r">{this.state.user && this.state.user.waitEdit}</div>
                                </div>

                            </div>
                        </div>
                        <div className="title">快捷入口</div>
                        <div className="listbox">
                            <div className="list">

                                {
                                    this.state.user.permissionList.indexOf(29) == -1 ?
                                        <div className='active normal'>
                                            <img src={require('../../assets/images/img-home-look.png')} alt="" />
                                            <div className="flex">
                                                <p >查看数据</p>
                                                <p>各种数据 , 方便查看</p>

                                            </div>
                                            <img className='noAdmin' src={require('../../assets/images/img-home-noAdmin.png')} alt="" />

                                        </div> : <Link to='/Statistics'>
                                            <img src={require('../../assets/images/img-home-look.png')} alt="" />
                                            <div className="flex">
                                                <p >查看数据</p>
                                                <p>各种数据 , 方便查看</p>

                                            </div>
                                        </Link>
                                }
                            </div>
                            <div className="list">

                                {
                                    this.state.user.permissionList.indexOf(14) == -1 ?
                                        <div className='active normal'>
                                            <img src={require('../../assets/images/img-home-addVideo.png')} alt="" />
                                            <div className="flex">
                                                <p >写视频文章</p>
                                                <p>添加视频写文章</p>

                                            </div>
                                            <img className='noAdmin' src={require('../../assets/images/img-home-noAdmin.png')} alt="" />

                                        </div> : <Link to='/Video/Edit'>
                                            <img src={require('../../assets/images/img-home-addVideo.png')} alt="" />
                                            <div className="flex">
                                                <p >写视频文章</p>
                                                <p>添加视频写文章</p>

                                            </div>

                                        </Link>
                                }
                            </div>
                            <div className="list">
                                {
                                    this.state.user.permissionList.indexOf(2) == -1 ?
                                        <div className='active normal'>
                                            <img src={require('../../assets/images/img-home-uploadStaff.png')} alt="" />
                                            <div className="flex">
                                                <p >上传员工</p>
                                                <p>一键上传员工 , 方便简单</p>

                                            </div>
                                            <img className='noAdmin' src={require('../../assets/images/img-home-noAdmin.png')} alt="" />

                                        </div> : <Link to='/Staff/Dept'>
                                            <img src={require('../../assets/images/img-home-uploadStaff.png')} alt="" />
                                            <div className="flex">
                                                <p >上传员工</p>
                                                <p>一键上传员工 , 方便简单</p>

                                            </div>

                                        </Link>
                                }

                            </div>
                            <div className="list">
                                {
                                    this.state.user.permissionList.indexOf(9) == -1 ?
                                        <div className='active normal'>
                                            <img src={require('../../assets/images/img-home-banner.png')} alt="" />
                                            <div className="flex">
                                                <p >轮播管理</p>
                                                <p>添加轮播文章 , 可开启或关闭文章</p>

                                            </div>
                                            <img className='noAdmin' src={require('../../assets/images/img-home-noAdmin.png')} alt="" />

                                        </div> : <Link to='/Article/Index/Banner'>
                                            <img src={require('../../assets/images/img-home-banner.png')} alt="" />
                                            <div className="flex">
                                                <p >轮播管理</p>
                                                <p>添加轮播文章 , 可开启或关闭文章</p>

                                            </div>

                                        </Link>
                                }

                            </div>
                            <div className="list">
                                {
                                    this.state.user.permissionList.indexOf(28) == -1 ?
                                        <div className='active normal'>
                                            <img src={require('../../assets/images/img-home-addShield.png')} alt="" />
                                            <div className="flex">
                                                <p >添加屏蔽词</p>
                                                <p>设置屏蔽词 , 提高文章质量</p>

                                            </div>
                                            <img className='noAdmin' src={require('../../assets/images/img-home-noAdmin.png')} alt="" />

                                        </div> : <Link to='/System'>
                                            <img src={require('../../assets/images/img-home-addShield.png')} alt="" />
                                            <div className="flex">
                                                <p >添加屏蔽词</p>
                                                <p>设置屏蔽词 , 提高文章质量</p>

                                            </div>

                                        </Link>
                                }

                            </div>
                            <div className="list">
                                {
                                    this.state.user.permissionList.indexOf(14) == -1 ?
                                        <div className='active normal'>
                                            <img src={require('../../assets/images/img-home-addArticle.png')} alt="" />
                                            <div className="flex">
                                                <p >写文章</p>
                                                <p>各种文章 , 由我编辑</p>

                                            </div>
                                            <img className='noAdmin' src={require('../../assets/images/img-home-noAdmin.png')} alt="" />

                                        </div> : <Link to='/Article/Edit'>
                                            <img src={require('../../assets/images/img-home-addArticle.png')} alt="" />
                                            <div className="flex">
                                                <p >写文章</p>
                                                <p>各种文章 , 由我编辑</p>

                                            </div>

                                        </Link>
                                }

                            </div>
                            <div className="list">
                                <Link to='/Article/Index'>
                                    <img src={require('../../assets/images/img-home-allArticle.png')} alt="" />
                                    <div className="flex">
                                        <p >您写的文章</p>
                                        <p>查看您写的各种文章</p>
                                    </div>
                                    <img className='noAdmin' src={require('../../assets/images/img-home-noAdmin.png')} alt="" />

                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default Home